body {
    height: 100vh;
    font-family: sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.App {
    height: 100vh;
}
.App-header {
    background: rgb(0,0,0);
    background: linear-gradient(90deg, rgb(27, 44, 70) 39%, rgb(120, 156, 206) 100%);
    font-family: monospace;
    height: 10vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(9px + 2vmin);
    font-weight: bolder;
    color: white;
    padding-top: 12px;
    border-bottom: 2px solid #333333;
    text-shadow: 2px 2px 2px #000000
}
.App-header p {
    font-size: 0.9rem;
    font-weight: lighter;
    opacity: 0.7;
}

#editor-container {
    width: 100%;
    max-width: 700px;
    float: right;
    height: 86vh;
}

#editor {
    font-family: "Lucida Console", "Courier New", monospace;
    font-size: 0.7rem;
    padding: 1.4rem;
    width: 100%;
    height: 72vh;
    background: linear-gradient(360deg, rgba(247,247,247,1) 23%, rgba(212,212,212,1) 99%);
    color: #070707;
    border: 2px solid #c0c0c0;
    outline: none;
}
#editor:hover {
    border: 2px solid #a3a8a9;
}

#preview-container {
    max-width: 700px;
    width: 100%;
    float: left;
    background-color: white;
}

#preview {
    background-color: white;
    border: 2px solid #f5f5f5;
    font-size: 0.8rem;
    padding: 1.2rem;
    overflow-y: auto;
    height: 79vh;
}

#preview:hover {
    border: 2px solid #dddede;
}

#preview pre {
    border: 1px solid lightgray;
    padding: 7px;
    background-color: white;
}
#preview td,
#preview th {
    border: 1px solid #224b4b;
    padding:6px;
}
#preview table {
    margin-bottom: 20px;
}
#preview img {
    max-width: 20vh;
    padding: 20px;
}

#button-bar {
    border-radius: 10px;
    border: 2px solid #d2d1d1;
    background: #dcdbdb;
    height: 60px;
    padding: 5px;
    width:100%;
    text-align: right;
    float: right;
    max-width: 800px;
    min-width: 300px;
}

#button-bar button {
    margin: 5px;
}

.sub-heading {
    background: #1b304d;
    border-radius: 10px;
    font-size: 1rem;
    text-shadow: 1px 1px 1px #000000;
    text-align: center;
    color: #f3f3f3;
    padding: 8px;
    border: 1px solid #565656;
}